import { Injectable } from '@angular/core';

import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

import { environment } from './../../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class UserService {

  private apiUrl = environment.apiUrl;

  constructor(
    private http: HttpClient
  ) { }

  checkOldPassword(postStr: { email: string, password: string }) {
    return this.http.post<any>(this.apiUrl + 'checkpass', postStr);
  }

  changePassword(postStr: { email: string, oldpassword: string, newpassword: string }) {
    return this.http.post<any>(this.apiUrl + 'changepass', postStr);
  }

  getDashboardData(userid) {
    return this.http.get<any>(this.apiUrl + 'userdata/profile/'+userid);
  }
}
