import { Component, OnInit } from '@angular/core';
import { State, Store } from '@ngrx/store';
import { UserState } from '../../store/app.state';
import { MatDialog } from '@angular/material/dialog';
import { ChangePasswordDialogComponent } from '../change-password-dialog/change-password-dialog.component';
import { User } from '../../shared/models/user';
import { environment } from '../../../environments/environment';
import { UserService } from '../services/user.service';
import { UserData } from '../../shared/models/user-data';

@Component({
  selector: 'app-user-dashboard',
  templateUrl: './user-dashboard.component.html',
  styleUrls: ['./user-dashboard.component.css']
})
export class UserDashboardComponent implements OnInit {
  public userState: User;
  public storagePath: string;
  public random_number: number;
  public userData: UserData = new UserData();

  constructor(
    private store: Store<any>,
    public dialog: MatDialog,
    private userService: UserService,
  ) { }

  ngOnInit() {
    this.random_number = Math.floor(Math.random() * (999999 - 100000)) + 100000;
    this.storagePath = environment.storageUrl;
    this.store.select(store => store.UserState.user).subscribe(user => {
      this.userState = user;

      //get user data
      if(this.userState.id){
        this.userService.getDashboardData(this.userState.id).subscribe((data: UserData) => {
          this.userData = data;
        });
      }
    });

  }

  changePass() {
    let dialogRef = this.dialog.open(ChangePasswordDialogComponent, { width: '40%', height: 'auto', maxHeight: '95%', data: { email: this.userState.email } });
  }


}
